<template>
  <div>
    <div class="input-field">
      <p class="">Find A Book</p>
      <input
        type="text"
        v-model="queryString"
        placeholder="Enter a book title or author here"
      />
      <button @click="findBooks" class="btn">Find Books</button>
    </div>
    <div id="books-container">
      <Book
        class="book"
        v-for="(book, i) in books"
        :key="i"
        :book="book"
        :page="page"
      />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Book from "./Book";

export default {
  name: "SearchBooks",
  props: ["page"],
  components: { Book },
  data() {
    return {
      books: [],
      queryString: "",
    };
  },
  methods: {
    async findBooks() {
      const data = await axios.post("api/book-search/find-books", {
        query: this.queryString,
      });
      this.books = data.data;
      this.queryString = "";
    },
  },
};
</script>

<style scoped>
.input-field {
  width: 50%;
  margin: 4em auto 6em auto;
}
.books-container {
  width: 100%;
}
.book {
  margin: 3em auto;
  width: 95%;
}

@media only screen and (min-width: 600px) {
  .book {
    width: 60%;
  }
}
</style>
