<template>
  <div class="book-card">
    <div class="card-top">
      <div class="image-container">
        <img
          v-if="book.image"
          class="book-image"
          :src="book.image.thumbnail"
          alt="image of the book cover"
        />
        <span v-else>Image Unavailable</span>
      </div>
      <div class="info-container">
        <h6 class="book-title">{{ book.title }}</h6>
        <p class="book-authors" v-for="(author, i) in book.authors" :key="i">
          {{ author }}
        </p>
        <p class="book-pageCount">Pages: {{ book.pageCount }}</p>
        <div>
          <a :href="book.preview" class="btn-small blue">Preview</a>
          <button
            class="btn-small"
            :class="deleteButtonClass"
            @click="assignMethod"
          >
            {{ saveOrDelete }}
          </button>
        </div>
      </div>
    </div>
    <p class="book-description">{{ book.description }}</p>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Book",
  props: ["book", "page", "bookIndex"],
  data() {
    return {
      error: "",
      saveSuccessful: "",
    };
  },
  methods: {
    async saveBook() {
      const book = {
        image: this.book.image,
        title: this.book.title,
        authors: this.book.authors,
        pageCount: this.book.pageCount,
        description: this.book.description,
      };
      const saveBook = await axios.post("api/book-search/save-book", { book });
      this.saveSuccessful = saveBook.status;
    },
    deleteBook() {
      axios.delete(`api/book-search/delete-book/${this.book._id}`);
      this.$emit("delete-book", this.bookIndex);
    },
  },
  computed: {
    assignMethod() {
      if (this.page === "find-books") {
        return this.saveBook;
      } else {
        return this.deleteBook;
      }
    },
    saveOrDelete() {
      if (this.page === "find-books") {
        return "Save";
      } else {
        return "Delete";
      }
    },
    deleteButtonClass() {
      if (this.page === "find-books") {
        return "green accent-4";
      } else {
        return "red";
      }
    },
  },
};
</script>

<style scoped>
.book-card {
  border-bottom: 1px solid #e6e6e6;
}
.card-top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.info-container {
  padding-left: 1em;
  width: 50%;
}
.book-description {
  width: 88%;
}
</style>
