<template>
  <div>
    <h5>Your Saved Books</h5>
    <p class="error" v-if="error">{{ error }}</p>
    <div class="books-container">
      <Book
        class="book"
        v-for="(book, i) in books"
        :key="i"
        :bookIndex="i"
        :book="book"
        :page="page"
        @delete-book="deleteBook"
      ></Book>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Book from "./Book";

export default {
  name: "SavedBooks",
  components: { Book },
  props: ["page"],
  data() {
    return {
      books: [],
      error: "",
      text: "",
    };
  },
  methods: {
    deleteBook(id) {
      this.books.splice(id, 1);
    },
  },
  async mounted() {
    const response = await axios.get("api/book-search/saved-books");
    this.books = response.data;
  },
};
</script>

<style scoped>
h5 {
  margin: 2em 0 2em 1em;
}
.input-field {
  width: 50%;
  margin: 4em auto 6em auto;
}
.books-container {
  width: 100%;
}
.book {
  margin: 3em auto;
  width: 95%;
}

@media only screen and (min-width: 600px) {
  .book {
    width: 60%;
  }
}
</style>
