<template>
  <div>
    <nav-bar></nav-bar>
    <title-container @change-page="changePage"></title-container>
    <saved-books v-if="page === 'saved-books'" :page="page"></saved-books>
    <search-books v-else :page="page"></search-books>
  </div>
</template>

<script>
import NavBar from "./components/NavBar";
import SavedBooks from "./components/SavedBooks";
import SearchBooks from "./components/SearchBooks";
import TitleContainer from "./components/TitleContainer";

export default {
  name: "App",
  components: {
    NavBar,
    SavedBooks,
    SearchBooks,
    TitleContainer,
  },
  data() {
    return {
      page: "find-books",
    };
  },
  methods: {
    changePage(page) {
      this.page = page;
    },
  },
};
</script>

